const Noticias = (props) => {
    const {price, percent_change_1h, percent_change_7d, percent_change_24h} = props.precio;

    return (
        <div className="row">
            {props.noticias.map((noticia) => (
                <Noticia
                    noticia={noticia}
                />
            ))}
        </div>
    );
}

export default Noticias;